        margin-right: 50px;
    }
    .track-item h2{
        margin-right: 20px;
        font-size: 25px;
    }
    .track-item div h3{
        font-size: 25px;
    }
    .buttons button{
        padding: 5px;
        width: 20%;
    }    
    .buttons .b-one{
        margin-left: 150px;
    }
    .buttons .b-two{
        margin-right: 150px;
    }    
}